.App{
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 1 1 0%;
      flex: 1 1 0%;
  padding: 10px;
  overflow: scroll;
  -ms-flex-negative: 0;
      flex-shrink: 0;
  min-height: 300px;
}

.Message{
  border: 1px solid green;
  border-radius: 5px;
  padding: 10px;
  margin:10px;
}

.Message-not-saved{
  color:#eee;
}

.Message-author{
  font-weight: bold;
}

.Message-text{
  margin-top:5px;
}

.ChannelList{
  width:200px;
  border: 1px solid black;
  display:-ms-flexbox;
  display:flex;
  height: 100%;
  -ms-flex-direction: column;
      flex-direction: column;
}

.ChannelList-item{
  height:30px;
  dislay: flex;
  -ms-flex-align: center;
      align-items: center;
  border-bottom: 1px solid navy;
  padding: 0 5px;
}

.ChannelList-item-selected{
  background-color: #00ffd0;
}

.ChannelList-item:hover{
  background-color: #00ffd0;
  cursor: pointer;
}



